          el.style.display = 'none';
          var doneElem = document.createElement('div');
          doneElem.style.marginTop = '5px';
          doneElem.style["float"] = 'right';
          doneElem.innerText = 'done';
          el.insertAdjacentElement('beforebegin', doneElem);
